@import '../../colors.scss';

.container {
	position: relative;
}

.input {
	width: 100%;
	color: $gray;
	border-radius: 8px;
	background-color: $lighten-blue;
	padding: 13px 80px 13px 16px;
	font-size: 14px;
	border: 1px solid transparent;

	&:focus {
		border: 1px solid $blue;
	}
}

.buttonsBlock {
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	right: 30px;
	top: 50%;
	transform: translateY(-50%);
}

.clearButton {
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 0 10px 0 0;
	width: 16px;
	height: 16px;
	border-radius: 50%;
	background-color: $gray;
}

.copyButton {
	display: flex;
	align-items: center;
	justify-content: center;
}
