@import '../../colors.scss';

.container {
	margin-bottom: 10px;
}
.block {
	background: var(--primary-color);
	color: white;
	padding: 20px;
	border-radius: 5px;
}

.idx {
	width: 20px;
	text-align: center;
}

.play {
	background-color: #ced4da !important;
	border-radius: 3px !important;
	height: 100%;
}

.rightBlock {
	position: relative;
	min-width: 50px;
}

.remove {
	position: absolute;
	right: -10px;
	top: -10px;
	color: white;

	&:hover {
		color: #eee;
	}
}

.timeTotal {
	position: absolute;
	font-size: 10px;
	right: -10px;
	bottom: -15px;
}

.wrapper {
	width: 250px;
	margin: 0 16px 16px 0;
}

.playerWrapper {
	position: relative;
	width: 100%;
	height: 140px;
	box-shadow: 0px 18px 40px -32px rgba(55, 39, 24, 0.55);
	border-radius: 16px;
	overflow: hidden;

	.video {
		width: 100%;
		height: 100%;
	}
}

.buttons {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: 0 0 4px 0;

	.checkbox {
		cursor: pointer;
	}

	button {
		color: $red;
	}
}

.playButton {
	position: absolute;
	z-index: 1;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	opacity: 0;
	transition: 0.3s;

	&.isVisible {
		opacity: 1;
	}
}
