@font-face {
	font-family: 'GilroyLight';
	src: url('./assets/fonts/Gilroy-Light.woff2') format('truetype');
	font-weight: bold;
}

@font-face {
	font-family: 'GilroyRegular';
	src: url('./assets/fonts/Gilroy-Regular.woff2') format('truetype');
	font-weight: bold;
}

@font-face {
	font-family: 'GilroyMedium';
	src: url('./assets/fonts/Gilroy-Medium.woff2') format('truetype');
	font-weight: bold;
}

* {
	box-sizing: border-box;
}

html,
body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	margin: 0;
	padding: 0;
}

body {
	font-family: GilroyRegular Arial, Helvetica, sans-serif;
}

p,
ul,
li,
h1,
h2,
h3 {
	margin: 0;
	padding: 0;
}

button,
input {
	padding: 0;
	border: none;
	outline: none;
	background-color: transparent;
}

button {
	cursor: pointer;
}

.playerPanel {
	padding: 80px 0 0 32px;
	flex: 1;
}

.editingPanel {
	padding: 10px;
	min-width: 0;
}

.container {
	margin-bottom: 50px;
	min-width: 100%;
}

.wrapper {
	display: flex;
	width: 100%;
}
