.cutsPanel {
	margin-top: 10px;
}

.cutsPanelBlank {
	height: 100px;
	align-items: center;
	vertical-align: middle;
}

.timeInput {
	max-width: 190px;
	padding: 0;
}

.input {
	text-align: center;
}

.stepBtn {
	border: 1px solid #ced4da;
	background: white;
}

.infoLine {
	padding-left: 20px;
	padding-bottom: 5px;
	margin: 5px 0;
	border-bottom: 1px solid #dfdfdf;

	> :first-child {
		margin-right: 15px;
		border-right: 1px solid #dfdfdf;
	}
}

.loadIcon {
	margin-right: 4px;
}

.bottomBtn {
	display: flex;
	flex-direction: row;
}

.cutsList {
	display: flex;
	flex-wrap: wrap;
	overflow: auto;
	align-content: flex-start;
}
