@import '../../colors.scss';

.container {
	height: 100%;
	position: relative;
}

.timeInput {
	height: 100%;
	width: 94px;

	div {
		height: 100%;
	}

	input {
		font-family: GilroyMedium;
		font-size: 18px;
		color: $gray;
		padding-left: 0;
		padding-right: 0;
	}

	fieldset {
		border: none !important;
	}
}

.buttons {
	position: absolute;
	top: 0;
	height: 100%;
	background-color: $light-gray;
	border-radius: 8px;
	overflow: hidden;

	.buttonsWrapper {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
		height: 100%;

		&:before {
			display: block;
			content: '';
			position: absolute;
			z-index: 3;
			left: 50%;
			top: 50%;
			width: 10px;
			height: 2px;
			transform: translate(-50%, -50%);
			background-color: #bdcce3;
		}

		button {
			z-index: 2;
			height: 50%;
			width: 28px;
			display: flex;
			align-items: center;
			justify-content: center;

			&:hover {
				background-color: $lighten-blue;
			}
		}

		.removeButton {
			transform: rotate(180deg);
		}
	}
}

.leftPosition {
	left: -150px;
}

.rightPosition {
	right: -150px;
}
