@import '../../colors.scss';

.tabItem {
	position: relative;
	z-index: 2;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 80px;
	height: 80px;
	border: 2px solid transparent;
	cursor: pointer;

	&.active {
		border: 2px solid $light-blue;
		border-right: 2px solid white;
		border-left: 2px solid transparent;

		&:before {
			display: block;
			content: '';
			position: absolute;
			width: 2px;
			height: 48px;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			background-color: $blue;
		}
	}
}

.icon {
	filter: grayscale(80%);
	opacity: 0.5;

	&.activeIcon {
		filter: grayscale(0);
		opacity: 1;
	}
}
