@import '../../colors.scss';

.button {
	color: #fff;
	font-size: 16px;
	border-radius: 8px;
	padding: 12px 24px;
	cursor: pointer;
	background-color: $red;
}

.withBorder {
	color: $gray;
	border: 1px dashed $gray;
	background-color: transparent;
}

.disabled {
	opacity: 0.6;
	cursor: not-allowed;
}

.blueTheme {
	background-color: $blue-with_opacity;
}
