.container {
	padding: 0 20px 0 0;
}

.header {
	height: 100px;
}
.timeRow {
	height: 70px;
	padding: 5px 10px 10px;
}

.buttons {
	display: flex;
	align-items: flex-end;
}

.playerWrapper {
	position: relative;
	padding-top: 56.25%;
	border-radius: 8px;
	overflow: hidden;
}

.previewButtons {
	padding: 10px;
	display: flex;
	justify-content: space-evenly;
}

.loadIcon {
	margin-right: 4px;
}

.recButton {
	position: relative;
	margin: 10px 0 0 0;
	width: 100%;
	border-radius: 8px;
	height: 66px;
	color: #fff;
	text-transform: uppercase;
	font-size: 18px;
	font-family: GilroyMedium;
}

.recButton::before {
	background: linear-gradient(70deg, #00a3ff, #ffdd40, #ae63e4, #47cf73, #0ebeff, #ffdd40, #ae63e4, #47cf73, #00a3ff);
	background-size: 200%;
	content: '';
	z-index: -1;
	position: absolute;
	width: calc(100% + 8px);
	height: calc(100% + 8px);
	top: -4px;
	left: -4px;
	border-radius: 8px;
	animation: recButtonAnimation 8s linear infinite;
}
.recButton {
	&.active::before {
		animation: recButtonAnimation 4s linear infinite;
	}
}

@keyframes recButtonAnimation {
	0% {
		background-position: 0%;
	}
	100% {
		background-position: 400%;
	}
}
